.screen-reader-text, .screen-reader-response {
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: polygon(0 0, 0 0, 0 0, 0 0);
  position: absolute !important;
  white-space: nowrap;
  height: 1px;
  width: 1px;
  overflow: hidden;

  &:focus {
    clip: auto;
    clip-path: none;
    display: block;
    height: auto;
    left: 5px;
    top: 5px;
    width: auto;
    z-index: 100000; /* Above WP toolbar. */
  }
}

.a11y-tools-trigger {
  display: block;
  background: $color-black;
  border-radius: 100%;
  color: $color-white;
  width: 50px;
  height: 50px;
  text-align: center;
  position: relative;

  i {
    position: absolute;
    left: 0;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
  }
}

.a11y-tools {
  height: 60px;
  overflow: hidden;
  transition: height 0.3s ease;

  @media #{$small-and-down} {
    height: 0;

    .a11y-tools-active & {
      height: 60px;
    }
  }

  html[data-contrast="true"] & {
    border-bottom: 1px solid $color-white;
  }

  .container {
    @media #{$small-and-down} {
      justify-content: flex-start;
    }
  }

  fieldset {
    border: none;
    padding: 0;
    margin: 0;

    @media #{$small-and-down} {
      padding: 5px 0;
    }

    legend, & > div {
      display: block;
      float: left;
      padding: 0 3px;
    }

    legend {
      width: 65px;
      font-size: 12px;
      padding-top: 8px;
    }

    label {
      display: block;
      text-indent: -100em;
      overflow: hidden;
      width: 50px;
      height: 50px;
      position: relative;
      background: $color-gray-light;
      transition: background 0.3s ease;
      cursor: pointer;

      &::after {
        position: absolute;
        left: 0;
        width: 100%;
        color: $color-black;
      }

      &:hover, &:focus {
        background: $color-gray-medium;

        &::after {
          color: $color-white;
        }
      }

      html[data-contrast="true"] & {
        background: $color-black;
        border: 1px solid $color-white;

        &::after {
          color: $color-white;
        }

        &:hover, &:focus {
          background: $color-white;

          &::after {
            color: $color-black;
          }
        }
      }
    }

    &.toggle-contrast {
      label {
        &::after {
          content: '';
          top: 0;
          height: 100%;
          background: url('../images/icon-contrast.svg') no-repeat 50% 50%;
        }
      }
    }

    &.text-size {
      label {
        &::after {
          content: 'A';
          top: 50%;
          transform: translateY(-50%);
          text-indent: 0;
          text-align: center;
          transition: color 0.3s ease;
          speak: none;
        }
      }

      div[class*="default-"] label::after {
        font-size: 18px;
      }

      div[class*="medium-"] label::after {
        font-size: 24px;
      }

      div[class*="large-"] label::after {
        font-size: 27px;
      }
    }

    input[type="radio"]:checked + label {
      background: $color-green-dark;

      &::after {
        color: $color-white;
      }

      html[data-contrast="true"] & {
        background: $color-gray-dark;
      }
    }

    input[type="checkbox"]:checked + label {
      background: $color-green-dark;

      &::after {
        background-image: url('../images/icon-contrast-inverse.svg');
      }

      html[data-contrast="true"] & {
        background: $color-gray-dark;

        &:hover, &:focus {
          background: $color-gray-medium;
        }
      }
    }
  }

  // TEMP: Hide form; TODO: Style form;
  form {
    display: none;
  }
}

html[data-contrast="true"] {
  background: $color-black;
  color: $color-white;

  .banner .navbar, .page-footer .footer-copyright {
    background: $color-black;
    border-top: 1px solid $color-white;
    border-bottom: 1px solid $color-white;
  }

  .navbar-menu li.current-page-ancestor, .navbar-menu li.current-menu-item,
  .navbar-menu li:hover, .navbar-menu li:focus, .navbar-menu li.hover {
    background: $color-gray-dark;
    border-right: 1px solid $color-white;
    border-left: 1px solid $color-white;
  }

  .btn, .social-icons li a, .page-footer .widget_nav_menu .menu li a {
    background: $color-black;
    border-color: $color-white;

    &:hover, &:focus {
      background: $color-white;
      color: $color-black;
    }
  }

  select, input[type="text"] {
    border-color: $color-white;
    background: $color-black;
    color: $color-white;
  }

  h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6, a {
    color: $color-white;
  }

  .banner .logo img {
    background: url('../images/DRNC-Logo-BW@2x.png') no-repeat;
    background-size: contain;
    display: block;
    box-sizing: border-box;
    padding-left: 100%;
    padding-top: 27.65%;
    overflow: hidden;
  }
}



